import React from 'react'
import ReactDOM from 'react-dom'
import { useSpring, animated } from 'react-spring'
import './styles.css'
import { Parallax, ParallaxLayer } from '@react-spring/parallax' // // Icons made by Freepik from www.flaticon.com

function Card() {
  const [{ y, color }, set] = useSpring(() => ({ y: 100, color: '#fff' }))

  return (
    <Parallax pages={2} style={{ top: '0', left: '0' }}>
      <ParallaxLayer offset={1} speed={2} style={{ backgroundColor: 'green' }} />

      <ParallaxLayer
        offset={0}
        speed={3}
        style={{ display: 'flex', justifyContent: 'center', alignItems: 'center' }}>
        <p>Scroll to Downlaod Spam</p>
      </ParallaxLayer>

      <ParallaxLayer offset={1} speed={2} style={{ backgroundColor: '#afd275' }} />

      <ParallaxLayer
        offset={1}
        speed={3}
        style={{
          display: 'flex',
          justifyContent: 'center',
          alignItems: 'center',
          color: 'black',
        }}>
        <button
          onMouseEnter={() => set({ y: 0, color: '#000' })}
          onMouseLeave={() => set({ y: 100, color: '#fff' })}>
          <animated.span style={{ color }}>Downlaod Spam</animated.span>
        </button>
      </ParallaxLayer>
    </Parallax>
  )
}

ReactDOM.render(<Card />, document.getElementById('root'))
